/**
 * @description html entity 문자열 이스케이프 처리(단, &, <, >, ', " 특수문자에 대해서만 처리함)
 * @param text 원본 텍스트
 * @returns 이스케이프 처리된 텍스트
 */
export const unsanitizeText = (text: string) => {
  const htmlEntity: Record<string, string> = {
    '&amp;': '&',
    '&lt;': '<',
    '&gt;': '>',
    '&quot;': '"',
    '&#39;': "'",
  };

  return text.replace(/&(amp|lt|gt|quot|#39);/g, (character) => htmlEntity[character]);
};
